import React from 'react';
import style from './Login.module.css'
import styleError from './../Common/InputForm.module.css'
import {Field, reduxForm} from "redux-form";
import InputForm from "../Common/InputForm";
import {maxLengthCreator, required} from "../../Utils/Validators";
import {LoginThunk} from "../../Redux/auth-reducer";
import {connect} from "react-redux";
import {Navigate} from "react-router-dom";
import {shortCutField} from "../Common/shortCutField";


const maxLength15 = maxLengthCreator(20)

 const LoginForm = (props) => {
    return(
            <form className={style.form} onSubmit={props.handleSubmit}>
                    {/* handleSubmit   !    ,
                          ,    
                        */}
                    {shortCutField('email',' ',[required, maxLength15],InputForm)}
                    {shortCutField('password',' ',[required, maxLength15],InputForm,'password')}
                    {shortCutField('rememberMe',null,[],'input',"checkbox",null,' ')}
                    {/*
                     
                    <div><Field name={'email'} placeholder={' '} validate={[required, maxLength15]} component={InputForm}/></div>
                    <div><Field name={'password'} type={'password'} placeholder={' '} validate={[required, maxLength15]} component={InputForm}/></div>
                    <div><Field type="checkbox" name={'rememberMe'} component={'input'}/> </div>*/}
                    <div><button></button></div>
                {props.error && <div className={styleError.error + " " + styleError.spanError}>{props.error}</div>}
                {/*    redux    ,   error   null      
                      */}
            </form>
    )
}

const LoginReduxForm = reduxForm({form: 'login'})(LoginForm)
/*  redux         c  form*/
const Login = (props) => {
    const onSubmit = (formData) => {

        props.LoginThunk(formData.email, formData.password, formData.rememberMe)
        //    ,     ,     

    }/*   ,      form e handleSubmit   ,
      onSubmit           */

    if(props.isAuth) return <Navigate to={'/dialogs'}/>
    return (
        <>
            <h1>Login Page</h1>
            <LoginReduxForm onSubmit={onSubmit}/>{/*   onSubmit  */}
        </>
    );
};

const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth
}) //  ,   !

 export default connect(mapStateToProps, {LoginThunk})(Login);

